import React, { Component } from 'react';
import { Layout, Breadcrumb,Tabs} from 'antd';
import ListContent from './ListContent'
import OldNew from './OldNew'

const { TabPane } = Tabs;
function callback(key) {
  console.log(key);
}

class NewList extends Component {
    state = {  }
    render() { 
        return ( 
             <Layout className="layout">
             <Breadcrumb style={{ padding: '2px',background:'#' }}>
        <Breadcrumb.Item>首页</Breadcrumb.Item>
        <Breadcrumb.Item>文章列表</Breadcrumb.Item>
      </Breadcrumb>
             <div className="card-container">
        <Tabs type="card" onChange={callback.bind(this)}>
          <TabPane tab="全部文章" key="1">
            <OldNew style={{background: 'white'}}/>
          </TabPane>
          <TabPane tab="已发表" key="2">
            <ListContent />
          </TabPane>
          <TabPane tab="草稿箱" key="3">
            <OldNew style={{background: 'white'}}/>
          </TabPane>
          <TabPane tab="回收站" key="4">
            <OldNew style={{background: 'white'}}/>
          </TabPane>
          <TabPane tab="博客配置" key="5">
            <OldNew style={{background: 'white'}}/>
          </TabPane>
        </Tabs>
      </div>
     </Layout>
         );
    }
}
 
export default NewList;